<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;体检
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" :rules="rules" label-width="160px">
          <el-form-item label="职位名称" prop="name" :label-width="formLabelWidth">
            <el-input v-model="form.name"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="招聘人数" prop="people" :label-width="formLabelWidth">
            <el-input v-model="form.people"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="薪资" prop="salary" :label-width="formLabelWidth">
            <el-input v-model="form.salary"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="地址" prop="address" :label-width="formLabelWidth">
            <el-input v-model="form.address"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="截止时间" prop="endtime" :label-width="formLabelWidth">
            <el-date-picker
              v-model="form.endtime"
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="工作要求" prop="wordrequire" :label-width="formLabelWidth">
            <el-input type="textarea" v-model="form.wordrequire"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="工作内容" prop="wordinfo" :label-width="formLabelWidth">
            <el-input type="textarea"  v-model="form.wordinfo" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item class="submit">
            <el-button type="primary" @click="onSubmit('form')">提交</el-button>
            <el-button @click="$router.go(-1)">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'

  export default {
    name: 'recruitC',
    data: function () {
      return {
        form: {},
        rules: {
          name: [{
            required: true,
            message: '请填写职位名称',
            trigger: 'blur'
          }],
          people: [{
            required: true,
            message: '请填写招聘人数',
            trigger: 'blur'
          }],
          salary: [{
            required: true,
            message: '请填写薪资',
            trigger: 'blur'
          }],
          address: [{
            required: true,
            message: '请填写地址',
            trigger: 'blur'
          }],
          endtime:[{
            required: true,
            message: '请选择截止时间',
            trigger: 'change'
          }],
          wordrequire: [{
            required: true,
            message: '请填写工作要求',
            trigger: 'blur'
          }],
          wordinfo: [{
            required: true,
            message: '请填写工作内容',
            trigger: 'blur'
          }],
        },
        formLabelWidth: '100px'
      }
    },
    created() {

    },
    activated(){
      this.getV()
    },
    methods: {
      getV() {
        var that = this;
        var params = {
          'datoken': that.$route.query.datoken,
        };
        that.apiPost('/admin/djzpone', params).then((res) => {
          this.handelResponse(res, (data) => {
            that.form = data.data;
          })
        })
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if(valid) {
            var that = this;
            var d = new Date(that.form.endtime);
            console.log(d);   //Sun Jan 20 2019 16:43:42 GMT+0800

            console.log(d.getTime());  //这个全输入时间戳
            var year = d.getFullYear(),
              month = d.getMonth()+1,
              day = d.getDate();
            var h = d.getHours();//12
            var m = d.getMinutes(); //12
            var s = d.getSeconds();
            if (h < 10) {
              h = "0" + h;
            }
            if (m < 10) {
              m = "0" + m;
            }
            if (s < 10) {
              s = "0" + s;
            }
            var params = {
              name:that.form.name,
              salary:that.form.salary,
              address:that.form.address,
              people:that.form.people,
              endtime:year+'-'+month+'-'+day+''+h+':'+m+':'+s,
              wordrequire:that.form.wordrequire,
              wordinfo:that.form.wordinfo,
              datoken:that.form.datoken
            };
            console.log(params)
            that.apiPost('/admin/djzpupdate', params).then((res) => {
              this.handelResponse(res, (data) => {
                that.$message.success('成功');
                that.$router.go(-1);
              })
            })
          } else {
            return false;
          }
        });

      }
    },
    mixins: [http]
  }
</script>
<style>
  .el-upload--text {
    width: 180px !important;
  }
  .el-upload--text img {
    width: 100%;
    height: 100%;
  }
  textarea{
    height: 100px;
  }
