  /*  font-size: 28px;*/
  /*  color: #8c939d;*/
  /*  width: 500px;*/
  /*  height: 178px;*/
  /*  line-height: 178px;*/
  /*  text-align: center;*/
  /*}*/
  /*.el-upload--text{*/
  /*  width: auto;*/
  /*  height:auto ;*/
  /*  border: none;*/
  /*}*/
  /*.avatar {*/
  /*  width: 500px;*/
  /*  height: 178px;*/
  /*  display: block;*/
  /*}*/
</style>



